<template>
    
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Корзина
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{ cartAmount }} товара
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <ul class="cart__list">
           
              <cart-item v-for="item in products" :key="item.productId" :item="item" />
           
          </ul>
        </div>

        <div class="cart__block" v-if="cartAmount">
          <p class="cart__desc">
            Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span>{{ fullPrice | numberFormat }} ₽</span>
          </p>

          <router-link tag="button" class="cart__button button button--primery" type="submit" :to="{name: 'order'}" >
            Оформить заказ
          </router-link>
        </div>
      </form>
    </section>
  </main>
</template>
<script>
import  numberFormat from '@/helpers/numberFormat'
import { mapGetters } from 'vuex'
import CartItem from '@/components/CartItem'

export default {
    data() {
        return {
            
        }
    },
    filters: {
        numberFormat
    },
    computed: {
        ...mapGetters({ products: 'cartProductDetails', fullPrice: 'cartTotalPrice', cartAmount: 'cartAmount'})
    },
    components: {
        CartItem
    }
}
</script>